.button {
  font-family: inherit;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  border: none;
  text-decoration: none;
}

.button--order {
  padding: 10px 94px;
  background-color: @button-color;
  color: @base-color;
  min-width: 280px;

  &:hover {
    background-color: @hover-color;
    cursor: pointer;
  }

  &:active {
    background-color: @hover-color;
    color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
  }
}


.button--form {
  padding: 10px 78px;
  background-color: @button-color;
  color: @base-color;
  margin-bottom: 18px;

  &:hover {
    background-color: @hover-color;
    cursor: pointer;
  }

  &:active {
    background-color: @hover-color;
    color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
  }
}

.button--show-more {
  background-color: @focus-color;
}

.button--main {
  background-color: @button-color;
  color: @base-color;
  font-size: 14px;

  &:hover {
    background-color: @hover-color;
    cursor: pointer;
  }

  &:active {
    background-color: @hover-color;
    color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
  }
}


@media (min-width: @tablet-width) {
  .button--order {
    margin-bottom: -4px;
    margin-left: 25px;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 10px 93px;
    min-width: 180px;
  }

  .button--main {
    font-size: 20px;
  }

  .button--form {
    font-size: 20px;
    padding: 16px 65px;
  }
}

@media (min-width: @desctop-width) {
  .button--order {
    margin-bottom: 0;
    margin-top: 9px;
    margin-left: 25px;
    min-width: 150px;
    padding: 10px 52px;
  }

  .button--form {
    padding: 16px 209px;
  }
}
